import React from 'react';
import { DividerLine, IntroSection } from '../SharedComponents.jsx';
import ReactMarkdown from 'react-markdown';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';

export const ShowcaseComponent = ({ title, titleDescription, prompt, chatData, chatDescription }) => {
    return (
        <div>
            <IntroSection title={ `${title} Prompt` }>
                { titleDescription }
            </IntroSection>
            <DividerLine />
                <div className="box-style markdown-container">
                    <ReactMarkdown>{ prompt }</ReactMarkdown>
                </div>
            <DividerLine />
            <IntroSection title="Simulated Chat Conversation">
                { chatDescription }
            </IntroSection>
            <DividerLine />
            { chatData }    
        </div>
    );
};



export const ShowcaseComponentTabs = ({ title, titleDescription, tabs, chatData, chatDescription }) => {
    return (
        <div>
            <IntroSection title={ `${title} Prompt` }>
                { titleDescription }
            </IntroSection>
            <DividerLine />
            {/* Render Dynamic Tabs */}
            <Tabs>
                <TabList>
                    {tabs.map((tab, index) => (
                        <Tab key={ index }>{ tab.label }</Tab>
                    ))}
                </TabList>
                { tabs.map((tab, index) => (
                    <TabPanel key={ index }>
                        <div className="box-style markdown-container">
                            { typeof tab.content === 'string' ? (
                                <ReactMarkdown>{ tab.content }</ReactMarkdown>
                            ) : (
                                tab.content
                            )}
                        </div>
                    </TabPanel>
                ))}
            </Tabs>
            <DividerLine />
            <IntroSection title="Simulated Chat Conversation">
                { chatDescription }
            </IntroSection>
            <DividerLine />
            { chatData }    
        </div>
    );
};